 <!--品牌列表-->
<template>
   <div class="cms-list-layout">
    <cms-tabs>
         <cms-tab to="/brand/list">品牌列表</cms-tab>
         <cms-tab to="/brand/audit">待审核品牌</cms-tab>
    </cms-tabs>
    <cms-prompt collapse='true'>
          <li>显示商家申请品牌列表信息</li>
          <li>可对品牌列表进行审核操作</li>
          <!-- <li>{{sss}}</li> -->
    </cms-prompt>
        <!-- 筛选区 -->
    <section class="cms-list-query">
         <div class="query-group">
           <label >品牌名称</label>
           <el-input type="textarea " v-model="queryParams.name" @keyup.native.enter='query'></el-input>
         </div>
         <div class="query-group">
           <label>审核状态</label>
           <el-select v-model="queryParams.status" @change="query">
             <el-option label="全部" value=""></el-option>
             <el-option label="待审核" :value="2"></el-option>
             <el-option label="审核不通过" :value="3"></el-option>
             <el-option label="审核通过" :value="4"></el-option>
           </el-select>
         </div>
         <div class="query-group">
          <cms-button size="small" :colorPicker='true'  @click="query">查询</cms-button>
         </div>
     </section>
      <!-- 操作区 -->
      <cms-sticky>
       <!-- 弹出框 -->
      <el-dialog title="审核确认" :visible.sync="dialogFormVisible" :before-close="handleClose">
        <el-form ref="form" :model="dialogForm" :rules="saveRules">
        <el-form-item label="原因" prop="remark" label-width="120px" :error="errorMessage.remark">
        <el-input type="textarea" v-model="dialogForm.remark" auto-complete="off" rows="6"></el-input>
        </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button type="primary" size="mini" @click="noPass(dialogForm.id, dialogForm.remark)">确 定</el-button>
        </div>
          </el-dialog>
    </cms-sticky>
      <el-table :data="tableData" style="width: 100%" stripe v-loading="loading" @selection-change='getCheckItems'
        @row-click='checkRows' ref='dataTable'>
        <el-table-column type="selection" width="46" fixed='left'></el-table-column>
        <el-table-column prop="id" label="id" width="60"></el-table-column>
        <el-table-column prop="name" label="品牌名称" min-width="110">
          <template slot-scope="scope">
            <div class="text-overflow" :title="scope.row.name"> {{scope.row.name}}</div>
          </template>
        </el-table-column>
        <el-table-column prop="initials" label="首字母" min-width="110" ></el-table-column>
        <el-table-column  label="LOGO" min-width="130" prop="logo">
          <div slot-scope="scope" class="table-img">
            <img v-if="scope.row.resourcesSpaceData" :src="$cms.filterUrl(scope.row.resourcesSpaceData.url) " alt="logo">
          </div>
        </el-table-column>
        <el-table-column prop="status" label="状态" min-width="110" >
            <div slot-scope="scope">
              <span class="success" v-if="scope.row.status === 2">待审核</span>
              <span class="error" v-if="scope.row.status === 3">审核不通过</span>
              <span class="success" v-if="scope.row.status === 4">审核通过</span>
            </div>
        </el-table-column>
        <el-table-column prop="remark" label="备注(原因)" min-width="110" >
          <template slot-scope="scope">
            <div class="text-overflow" :title="scope.row.remark"> {{scope.row.remark}}</div>
          </template>
        </el-table-column>
        <el-table-column  label="操作" width='200' prop="status"  fixed="right" align="center">
          <div slot-scope="scope">
            <div v-if="scope.row.status === 2">
              <cms-button icon='edit' size='small' type='info' @click="audit(scope.row.id)">审核</cms-button>
              <cms-button icon='error' size='small'  type='info' @click="handleEdit(scope.$index, scope.row)">审核不通过</cms-button>
            </div>
          </div>
        </el-table-column>
     </el-table>
     <cms-pagination @change="getPage" :total='total'></cms-pagination>
   </div>
</template>
<script>
import rules from '@/rules'
import list from '@/mixins/list'
import form from '@/mixins/form.js'
import {
  brandList,
  brandAdopt,
  brandNot,
  brandDelete
} from '@/api/product/brand.js'
var defaultData = {
  'name': '',
  'status': '',
  'queryType': 2,
  'page': 1,
  'size': 10
}
export default {
  mixins: [list, form],
  data() {
    return {
      dialogFormVisible: false,
      queryParams: Object.assign({}, defaultData),
      dialogForm: {
        id: '',
        remark: ''
      },
      saveRules: {// 审核不通过规则
        remark: [rules.required('请填写不通过原因'), rules.len(1, 500)]
      }
    }
  },
  methods: {
    audit(id) {
      this.$confirm('您确定要审核这个品牌吗', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        brandAdopt({ id: id, status: 4 }).then(res => {
          if (res.code === 200) {
            this.$successMessage('审核成功')
            this.getTableData(brandList, this.queryParams)
          } else {
            this.$errorMessage('审核失败,' + res.message)
          }
        })
      })
        .catch(() => {})
    },
    // 审核不通过方法
    handleEdit: function(index, row) {
      this.dialogFormVisible = true
      this.dialogForm = Object.assign({}, row)
    },
    // 审核不通过
    noPass(id, remark) {
      this.$refs['form'].validate((valid) => {
        if (valid) {
          brandNot({ id: id, remark: remark }).then(res => {
            if (res.code === 200) {
              this.$successMessage('操作成功')
              this.getTableData(brandList, this.queryParams)
            } else {
              this.$errorMessage(res.message)
            }
            this.dialogFormVisible = false
          })
        } else {
          return false
        }
      })
    },
    handleClose() {
      this.$refs['form'].resetFields()
      this.dialogFormVisible = false
    },
    deleteDatas() {
      this.deleteTableDatas(brandDelete, this.ids) // 这个才是真实的删除方法
    }
  },
  activated() {
    // 缓存触发
    if (this.$route.query.type === 'list') {
      this.queryParams = Object.assign({}, defaultData)
      // 这个值一定要用动态的变量
      this.getTableData(brandList, this.queryParams)
    }
  },
  created() {
    // 首次触发
    this.getTableData(brandList, this.queryParams)
  }
}
</script>
<style>

</style>



